.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			@include max-lg {
				transform: translate(-50%, -40%);
			}
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 36px;
			font-family: $font-secondary;
			@include max-md {
				width: 32px;
			}
		}
	}
	.couple-wrapper {
		position: relative;
		background-color: var(--color-primary);
		padding: 5rem 0;
		@include max-md {
			padding: 5rem 0 3rem;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		overflow: hidden;
		&.man {
			text-align: left;
		}
		&.woman {
			text-align: right;
		}
		.image-wrap {
			position: relative;
			width: 90%;
			margin: 0 auto;
			@include max-md {
				width: 100%;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.frame {
					position: relative;
					width: 100%;
					height: auto;
				}
				.couple-image {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					aspect-ratio: 7/8;
					width: 70%;
					height: auto;
					object-fit: cover;
					border-radius: 50%;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-light);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-secondary);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
