.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		background-color: var(--color-primary);
		padding: 3rem;
		border-radius: var(--border-radius);
		text-align: center;
		margin-top: -5rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
