.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		margin: 0 auto;
		z-index: 5;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 70%;
				height: auto;
				aspect-ratio: 3/4;
				object-fit: cover;
				border-radius: 50%;
			}
			.frame {
				position: relative;
				width: 100%;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			border-radius: var(--border-radius);
			background-color: var(--color-primary);
			padding: 10rem 2rem 3rem;
			margin-top: -8rem;
			@include max-md {
				padding: 8rem 1rem 2rem;
				margin-top: -6rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
